import $ from 'jquery'

if (ENV.ping_url) {
  const interval = setInterval(() => {
    if (!document.hidden) {
      $.post(ENV.ping_url).fail(xhr => {
        if (xhr.status === 401) clearInterval(interval)
      })
    }
  }, 1000 * 180)
}
